<template>
    <div>
        <button type="button" class="btn btn-lg btn-style9 rounded-circle " @click="counter=0;" style="position:fixed;bottom:30px;right:30px;"><img src="https://upload.egov.balangankab.go.id/svg/arrow-loop.svg" 
             class="d-inline" style="height:50px;width:30px;" alt=""></button>
        <div style="width:100vw;height:100vh;background: rgb(154,251,143);
background: radial-gradient(circle, rgba(154,251,143,0.7544059860272234) 0%, rgba(26,173,30,0.6619690112373074) 100%);" @click="counter++;">
            <div style="height:100%;font-size:40vw;" class="d-flex justify-content-center align-items-center shadow-lg rounded-lg">
                <div>
                    {{counter}}
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default{
    layout:'app_islamic',
    data(){
        return{
            counter:0
        }
    },
    mounted() {
        let that=this;
     function onBackKeyDown(e){
          e.preventDefault();
          e.stopPropagation();
          that.$router.push('/app/islamic')
        };
        document.addEventListener("backbutton", onBackKeyDown, false);
    },
}
</script>